import React from 'react'
import { Field } from 'formik'
import { Row, Col, ButtonGroup, Button } from 'react-bootstrap'
import { Translation, useTranslation } from 'react-i18next'
import Oskari from '../../util/Oskari'
import SelectInput from '../inputs/SelectInput'
import digiroadStatus from '../../models/digiroadStatus'
import { toast } from 'react-toastify'

const Page2 = props => {
  const { formPage, setFormPage, values, setTouched } = props
  const { t } = useTranslation()

  if (formPage !== 2) return null

  const handlePageSubmit = async () => {
    const mapSchema = digiroadStatus.schemaAsArray.reduce((aggregate, item) => {
      return { ...aggregate, [item]: true }
    }, {})
    setTouched(mapSchema)
    try {
      await digiroadStatus.schema.validate(values)

      if (values.tiekuntaDigiroadissa === 'true') {
        setFormPage(3)
      } else {
        setFormPage(4)
      }
    } catch (exeption) {
      toast.warning(t('common.pagevalidationerror'))
    }
  }

  return (
    <Row>
      <Col>
        <Row className="mb-3 mt-3">
          <Col>
            <h2>
              <Translation>{t => t('map.verify_information')}</Translation>
            </h2>
          </Col>
        </Row>

        {/* Embedded Oskari Iframe */}
        <Oskari />

        <Field
          name="tiekuntaDigiroadissa"
          required
          options={[
            { value: '', text: 'map.select' },
            { value: true, text: 'map.already_submitted_no_changes' },
            { value: false, text: 'map.submit_changed_or_missing_info' }
          ]}
          label="map.verify_information"
          info="map.verify_information_info"
          component={SelectInput}
        ></Field>
        <Row>
          <Col>
            <ButtonGroup>
              <Button onClick={() => setFormPage(formPage - 1)}>
                <Translation>{t => t('common.previous')}</Translation>
              </Button>
              <Button onClick={() => handlePageSubmit()}>
                <Translation>{t => t('common.next')}</Translation>
              </Button>
            </ButtonGroup>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}

export default Page2
