import React from 'react'
import { Form, Row, Col } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import InfoSybmol from './utils/InfoSymbol'

const TextInput = props => {
  const { t } = useTranslation()
  const { label, info, required, type } = props
  const { field, form } = props
  const { errors, touched } = form

  const error = errors[field.name] && touched[field.name] ? errors[field.name] : false
  const isValid = !error && touched[field.name]

  return (
    <Form.Group as="div" controlId={field.name}>
      <Row>
        <Col>
          <Form.Label>
            {t(label)}
            &nbsp;
            {required && <span className="text-muted">({t('common.required')})</span>}
          </Form.Label>
        </Col>
        {info && (
          <Col className="col-auto">
            <InfoSybmol title={label} content={info} />
          </Col>
        )}
      </Row>
      <Form.Control
        as={type || 'input'}
        rows="5"
        {...props}
        {...field}
        value={field.value || ''}
        placeholder={t(label)}
        isInvalid={error}
        isValid={isValid}
      ></Form.Control>
      <Form.Control.Feedback type="invalid">{t('common.required')}</Form.Control.Feedback>
    </Form.Group>
  )
}

export default TextInput
