import React from 'react'
import { useTranslation } from 'react-i18next'
import { Col, Row, ButtonGroup, Button, Card, Spinner } from 'react-bootstrap'
import { Field } from 'formik'
import ReCAPTCHA from 'react-google-recaptcha'
import SummaryInfoField from './SummaryInfoField'
import DeliveryMethod from './DeliveryMethod'
import CheckBoxInput from '../inputs/CheckBoxInput'

const Page4 = props => {
  const { t } = useTranslation()
  const { formPage, setFormPage, values, setValues, isSubmitting } = props

  if (formPage !== 5) return null

  return (
    <Row>
      <Col>
        <Row className="mb-3">
          <Col>
            <h2>{t('summary.title')}</h2>
          </Col>
        </Row>
        <Card className="p-3">
          <SummaryInfoField title="tiekunta.tiekunta" content={values.tiekunta} />
          <SummaryInfoField title="tiekunta.municipality" content={values.kunta} />
          <SummaryInfoField
            title="tiekunta.kayttooikeusyksikkotunnus"
            content={values.kayttooikeusyksikkotunnus}
          />
          <SummaryInfoField title="tiekunta.informants_name" content={values.ilmoittaja} />
          <SummaryInfoField title="tiekunta.phone_number" content={values.puhelinnumero} />
          <SummaryInfoField title="tiekunta.email_address" content={values.email} />
          <SummaryInfoField title="tiekunta.suhde_tiehen" content={values.ilmoittajaSuhde} />
        </Card>

        {values.tiekuntaDigiroadissa === 'true' && (
          <>
            <Row className="mt-5 mb-5">
              <Col>
                <Row>
                  <h4>{t('map.already_submitted_no_changes')}</h4>
                </Row>
              </Col>
            </Row>
            {values.roads.map((road, index) => {
              return (
                <SummaryInfoField
                  key={index}
                  title="tie.road_name_2"
                  content={road.tiekunnanTienNimi}
                />
              )
            })}
          </>
        )}

        {values.tiekuntaDigiroadissa === 'false' &&
          values.roads.map((road, index) => (
            <Card key={index} className="p-3 mt-3">
              <Card.Header>
                {`${t('tie.added_road')} (${index + 1}/${values.roads.length})`}
              </Card.Header>
              <Card.Body>
                <SummaryInfoField title="tie.road_name" content={road.tiekunnanTienNimi} />
                <SummaryInfoField
                  title="tie.painorajoitus"
                  content={
                    road.painorajoitus
                      ? t('summary.painorajoitus_true') + ` - ${road.painorajoitus_suurin_massa}`
                      : t('summary.painorajoitus_false')
                  }
                />
                {road.painorajoitus && (
                  <SummaryInfoField
                    title="tie.painorajoitus_lisakilvet"
                    content={road.painorajoitus_lisakilvet}
                  />
                )}
                <SummaryInfoField
                  title="tie.toistuva_kelirikko"
                  content={road.kelirikkoToistuva ? t('common.yes') : t('common.no')}
                />
                {road.kelirikkoToistuva && (
                  <>
                    <SummaryInfoField
                      title="tie.kelirikon_alkaminen"
                      content={`${t(`tie.kelirikko.${road.kelirikkoAlkuKuukausi}`)} ${t(
                        `tie.kelirikko.${road.kelirikkoAlkuAika}`
                      )} `}
                    />
                    <SummaryInfoField
                      title="tie.kelirikon_paattyminen"
                      content={`${t(`tie.kelirikko.${road.kelirikkoLoppuKuukausi}`)} ${t(
                        `tie.kelirikko.${road.kelirikkoLoppuAika}`
                      )} `}
                    />
                  </>
                )}
                <SummaryInfoField
                  title="tie.ajokielto"
                  content={road.ajokielto ? t('common.yes') : t('common.no')}
                />
                {values.ajokielto && (
                  <SummaryInfoField
                    title="tie.ajokiellon_lisakilvet"
                    content={road.ajokieltoLisakilvet}
                  />
                )}
                <SummaryInfoField
                  title="tie.ajoeste_tiella"
                  content={road.ajoesteTiella ? t('common.yes') : t('common.no')}
                />
                {road.ajoesteTyyppi === 'avattava puomi' && (
                  <SummaryInfoField title="tie.ajoesteen_tyyppi" content={t('tie.ajoeste1')} />
                )}
                {road.ajoesteTyyppi === 'suljettu yhteys' && (
                  <SummaryInfoField title="tie.ajoesteen_tyyppi" content={t('tie.ajoeste2')} />
                )}
                <SummaryInfoField title="tie.nopeusrajoitus" content={road.nopeusRajotus} />
                <SummaryInfoField title="tie.karttalinkit" content={road.karttaLinkit} />
                <SummaryInfoField
                  title="tie.muut_merkit_ja_rajoitukset"
                  content={road.muutTiedot}
                />
              </Card.Body>
            </Card>
          ))}

        {isSubmitting ? (
          <Row className="justify-content-center m-5">
            <Spinner
              style={{ width: '5rem', height: '5rem' }}
              animation="border"
              role="status"
              size="lg"
              variant="primary"
            >
              <span className="sr-only">Loading...</span>
            </Spinner>
          </Row>
        ) : (
          <>
            <Field email={values.email} name="toimitusTapa" component={DeliveryMethod}></Field>
            <Field
              name="tietojenOikeellisuus"
              label="summary.tietojenOikeellisuus"
              component={CheckBoxInput}
            ></Field>
            <Row className="mb-4 justify-content-center">
              <ReCAPTCHA
                sitekey="6LdQjsAUAAAAAAZ3kIzTwPPfJzeKY5dgU4RWoghB"
                onChange={value => {
                  setValues({ ...values, reCAPTCHA: value })
                }}
              />
            </Row>
            <div className="d-flex flex-column mb-5">
              <ButtonGroup size="lg">
                <Button onClick={() => setFormPage(1)}>{t('summary.muuta_vastauksia')}</Button>
                <Button
                  variant="success"
                  type="submit"
                  disabled={
                    !values.tietojenOikeellisuus || !values.toimitusTapa || !values.reCAPTCHA
                  }
                >
                  {t('common.submit')}
                </Button>
              </ButtonGroup>
            </div>
          </>
        )}
      </Col>
    </Row>
  )
}

export default Page4
