import React, { useState } from 'react'
import { withFormik, Form as FormikForm } from 'formik'
import model from '../../models'
import FileSaver from 'file-saver'
import { submitPrivateRoad } from '../../services/privateRoadService'
import Page1 from './Page1'
import Page2 from './Page2'
import Page3a from './Page3a'
import Page3b from './Page3b'
import Page4 from './Page4'

const PrivateRoadForm = props => {
  const [formPage, setFormPage] = useState(1)

  return (
    <FormikForm>
      <Page1 {...props} formPage={formPage} setFormPage={setFormPage} />
      <Page2 {...props} formPage={formPage} setFormPage={setFormPage} />
      <Page3a {...props} formPage={formPage} setFormPage={setFormPage} />
      <Page3b {...props} formPage={formPage} setFormPage={setFormPage} />
      <Page4 {...props} formPage={formPage} setFormPage={setFormPage} />
    </FormikForm>
  )
}

const FormikEnhncer = withFormik({
  validationSchema: model.privateRoadYupSchema,
  mapPropsToValues() {
    return model.privateRoadYupInitialValues
  },
  async handleSubmit(values, formikBag) {
    const kuukaudet = [
      '',
      'Tammikuu',
      'Helmikuu',
      'Maaliskuu',
      'Huhtikuu',
      'Toukokuu',
      'Kes√§kuu',
      'Hein√§kuu',
      'Elokuu',
      'Syyskuu',
      'Lokakuu',
      'Marraskuu',
      'joulukuu'
    ]

    const roads = values.roads.map(road => {
      return {
        ...road,
        kelirikkoAlkuKuukausi: kuukaudet[road.kelirikkoAlkuKuukausi],
        kelirikkoLoppuKuukausi: kuukaudet[road.kelirikkoLoppuKuukausi]
      }
    })
    values.roads = roads

    const response = await submitPrivateRoad(values)

    FileSaver(response.data, 'Digiroad_tosite.pdf')

    formikBag.props.setPage(2)
  }
})(PrivateRoadForm)

export default FormikEnhncer

