import React from 'react'
import { Form, Row, Col } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'

const CheckBoxInput = props => {
  const { t } = useTranslation()
  const { label } = props
  const { field, form } = props
  const { errors, touched } = form

  const error = errors[field.name] && touched[field.name] ? errors[field.name] : false
  const isValid = !error && touched[field.name]

  return (
    <Form.Group>
      <Row>
        <Col xs={12}>
          <Form.Check
            {...props}
            {...field}
            isInvalid={error}
            isValid={isValid}
            label={t(label)}
          ></Form.Check>
        </Col>
      </Row>
    </Form.Group>
  )
}

export default CheckBoxInput
