import * as Yup from 'yup'

const schemaObject = {
  tiekunta: Yup.string()
    .min(3)
    .max(50)
    .required(),
  kunta: Yup.string()
    .min(2)
    .max(30)
    .required(),
  kayttooikeusyksikkotunnus: Yup.string()
    .max(30)
    .optional(),
  ilmoittaja: Yup.string()
    .min(5)
    .max(40)
    .required(),
  puhelinnumero: Yup.string()
    .min(10)
    .max(15)
    .required(),
  email: Yup.string()
    .email()
    .optional(),
  ilmoittajaSuhde: Yup.string().optional()
}

const initialValues = {
  tiekunta: '',
  kunta: '',
  kayttooikeusyksikkotunnus: '',
  ilmoittaja: '',
  puhelinnumero: '',
  email: '',
  ilmoittajaSuhde: ''
}

// Page schema for one page at a time validation
export const schema = Yup.object().shape(schemaObject)
export const schemaAsArray = Object.keys(schemaObject)

export default { schema, schemaAsArray, initialValues, schemaObject }
