import React from 'react'
import { Row, Col, Form, Image, Button } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import BannerImage from '../img/YT_banneri.PNG'

const LandingPage = ({ setPage }) => {
  const { t } = useTranslation()
  return (
    <Row>
      <Col>
        <Row>
          <Col>
            <Image src={BannerImage} fluid></Image>
          </Col>
        </Row>

        <Row className="mb-3 mt-3">
          <Col className="text-center">
            <h1>{t('landing.welcome')}</h1>
          </Col>
        </Row>

        <Row className="mb-5">
          <Col>
            <Form.Group controlId="lomakeOhje" align="center">
              <p>{t('landing.body')}</p>
              <p>{t('landing.body_notice')}</p>
              <a target="_blanc" href={t('landing.link_url')}>
                {t('landing.link_label')}
              </a>
            </Form.Group>
            <Row className="justify-content-md-center">
              <Button size="lg" onClick={() => setPage(1)}>
                {t('common.next')}
              </Button>
            </Row>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}

export default LandingPage
