import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'
import fi from './fi'
import sv from './sv'

/*
i18next-browser-languagedetector plugin is not used as
depending on the browser, it might return locales like "fi-FI" or only "fi"
from window.navigator and there is no easy way to shorten it for i18next to use.
*/
const options = {
  resources: { fi, sv },
  lng: (window.navigator.language || window.navigator.userLanguage).substr(0, 2),
  fallbackLng: 'fi',
  interpolation: { escapeValue: false }
}

function initCallback(err) {
  if (err) {
    return console.log('Error while initializing i18n', err)
  }

  /*
    Set first fallback language (options.fallbackLng) to language if translation file for users locale isn't provided.
    i18next doesn't change its language to fallback language if users locale isn't supported by the application
    but instead will resolve translations by using fallback languages.
    */
  if (!Object.keys(i18n.services.resourceStore.data).includes(i18n.language)) {
    i18n.changeLanguage(i18n.services.resourceStore.options.fallbackLng[0])
  }
}

i18n.use(initReactI18next).init(options, initCallback)

export default i18n
