import React from 'react'
import { Badge, Popover, OverlayTrigger } from 'react-bootstrap'
import { FaQuestion } from 'react-icons/fa'
import { useTranslation } from 'react-i18next'

const InfoSymbol = ({ title, content }) => {
  const { t } = useTranslation()
  return (
    <OverlayTrigger
      trigger="hover"
      placement="left"
      overlay={
        <Popover>
          <Popover.Title>{t(title)}</Popover.Title>
          <Popover.Content dangerouslySetInnerHTML={{ __html: t(content) }} />
        </Popover>
      }
    >
      <Badge>
        <FaQuestion />
      </Badge>
    </OverlayTrigger>
  )
}

export default InfoSymbol
