import React from 'react'
import { useTranslation } from 'react-i18next'
import PlaceHolder from '../img/oskari_placeholder.png'
import { Image, Row } from 'react-bootstrap'

const Oskari = () => {
  const { i18n } = useTranslation()

  // At the development time Oskari gives warnings. Placeholder prevents mixing up
  // this applications and Oskaris error messages during development.
  // eslint-disable-next-line no-undef
  if (process.env.NODE_ENV === 'development') {
    return (
      <Row className="justify-content-md-center mb-4">
        <Image src={PlaceHolder} fluid />
      </Row>
    )
  }

  const lang = i18n.language
  const url =
    'https://kehitysjulkinen.vayla.fi/oskari/?uuid=af456730-ce3e-4332-9175-bdc9636db361&lang=' +
    lang
  const style = {
    border: '1px solid blue',
    width: '100%',
    height: '500px'
  }
  const title = 'oskari'
  return <iframe title={title} src={url} style={style}></iframe>
}

export default Oskari
