import React from 'react'
import { useTranslation } from 'react-i18next'

const Language = () => {
  const { i18n } = useTranslation()

  return (
    <div className="text-right">
      <div className="btn-group btn-group-toggle">
        <label className={'btn btn-outline-primary ' + (i18n.language === 'fi' ? 'active' : '')}>
          suomeksi
          <input
            type="radio"
            name="lng_options"
            onChange={() => i18n.changeLanguage('fi')}
            defaultChecked={i18n.language === 'fi' ? true : false}
          />
        </label>
        <label className={'btn btn-outline-primary ' + (i18n.language === 'sv' ? 'active' : '')}>
          p√• svenska
          <input
            type="radio"
            name="lng_options"
            onChange={() => i18n.changeLanguage('sv')}
            defaultChecked={i18n.language === 'sv' ? true : false}
          />
        </label>
      </div>
    </div>
  )
}

export default Language
