import React from 'react'
import { Field } from 'formik'
import TextInput from '../inputs/TextInput'
import { Row, Col, Button } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { toast } from 'react-toastify'
import { schema, schemaAsArray } from '../../models/maitenanceAssociation'

const Page1 = props => {
  const { t } = useTranslation()
  const { values, setTouched, formPage, setFormPage } = props

  if (formPage !== 1) return null

  const handlePageSubmit = async () => {
    const mapSchema = schemaAsArray.reduce((aggregate, item) => {
      return { ...aggregate, [item]: true }
    }, {})
    setTouched(mapSchema)
    try {
      await schema.validate(values)
      // await page1Yup.validate(values)
      setFormPage(formPage + 1)
    } catch (exeption) {
      toast.warning(t('common.pagevalidationerror'))
    }
  }

  return (
    <Row>
      <Col>
        <Row className="mb-3 mt-3">
          <Col>
            <h2>{t('tiekunta.title')}</h2>
          </Col>
        </Row>

        <Field
          name="tiekunta"
          required
          label="tiekunta.tiekunta"
          info="tiekunta.tiekunta_info"
          component={TextInput}
        ></Field>

        <Field
          name="kunta"
          required
          label="tiekunta.municipality"
          info="tiekunta.municipality_info"
          component={TextInput}
        ></Field>

        <Field
          name="kayttooikeusyksikkotunnus"
          label="tiekunta.kayttooikeusyksikkotunnus"
          info="tiekunta.kayttooikeusyksikkotunnus_info"
          component={TextInput}
        ></Field>

        <Field
          name="ilmoittaja"
          required
          label="tiekunta.informants_name"
          info="tiekunta.informants_name_info"
          component={TextInput}
        ></Field>

        <Field
          name="puhelinnumero"
          required
          label="tiekunta.phone_number"
          info="tiekunta.phone_number_info"
          component={TextInput}
        ></Field>

        <Field
          name="email"
          label="tiekunta.email_address"
          info="tiekunta.email_address_info"
          component={TextInput}
        ></Field>

        <Field
          name="ilmoittajaSuhde"
          type="textarea"
          label="tiekunta.suhde_tiehen"
          info="tiekunta.suhde_tiehen_info"
          component={TextInput}
        ></Field>

        <Row className="mb-5">
          <Col>
            <Button onClick={() => handlePageSubmit()}>{t('common.next')}</Button>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}

export default Page1
