import React from 'react'
import { Field, FieldArray } from 'formik'
import { Row, Col, Button, ButtonGroup, Form, Card } from 'react-bootstrap'
import { useTranslation, Translation } from 'react-i18next'
import { toast } from 'react-toastify'
import privateRoad from '../../models/privateRoad'
import TextInput from '../inputs/TextInput'
import CheckBoxInput from '../inputs/CheckBoxInput'
import SelectInput from '../inputs/SelectInput'

const Page3b = props => {
  const { t } = useTranslation()
  const { values, formPage, setFormPage } = props

  const handlePageSubmit = async () => {
    try {
      await privateRoad.schema.validate(values)
      setFormPage(5)
    } catch (exeption) {
      toast.warning(t('common.pagevalidationerror'))
    }
  }

  if (formPage !== 4) return null

  const monthOptions = [...Array(13).keys()].map(month => {
    return { value: month, text: `tie.kelirikko.${month}` }
  })

  return (
    <Row>
      <Col>
        <Row className="mb-3 mt-3">
          <Col>
            <h2>{t('map.submit_changed_or_missing_info')}</h2>
          </Col>
        </Row>
        <FieldArray
          name="roads"
          render={({ push, remove }) => (
            <>
              {values.roads.map((road, index) => (
                <Card key={index} className="mt-3">
                  <Card.Header>
                    <Row sm={10}>
                      <Col>
                        <h4>{`${t('tie.added_road')} (${index + 1}/${values.roads.length})`}</h4>
                      </Col>
                      <Col sm={2} className="d-flex flex-column">
                        <Button onClick={() => remove(index)} variant="danger">
                          {t('tie.poista_tie')}
                        </Button>
                      </Col>
                    </Row>
                  </Card.Header>
                  <div className="border">
                    <Col className="mt-3">
                      <Field
                        name={`roads.${index}.tiekunnanTienNimi`}
                        required
                        label="tie.road_name"
                        info="tie.road_name_info"
                        component={TextInput}
                      ></Field>
                    </Col>
                  </div>

                  <Col className="border">
                    <div className="mt-3">
                      <Field
                        name={`roads.${index}.painorajoitus`}
                        label="tie.ei_rajoituksia"
                        component={CheckBoxInput}
                      ></Field>
                    </div>
                    <Form.Group as="div">
                      <Row>
                        <Col>
                          <Field
                            name={`roads.${index}.painorajoitus_suurin_massa`}
                            label="tie.painorajoitus"
                            info="tie.painorajoitus_info"
                            component={TextInput}
                          ></Field>
                        </Col>
                        <Col>
                          <Field
                            name={`roads.${index}.painorajoitus_lisakilvet`}
                            label="tie.painorajoitus_lisakilvet"
                            info="tie.painorajoitus_lisakilvet_info"
                            component={TextInput}
                          ></Field>
                        </Col>
                      </Row>
                    </Form.Group>
                  </Col>

                  <Col className="border">
                    <div className="mt-3">
                      <Field
                        name={`roads.${index}.kelirikkoToistuva`}
                        label="tie.toistuva_kelirikko"
                        component={CheckBoxInput}
                      ></Field>
                    </div>

                    {values.roads[index].kelirikkoToistuva && (
                      <>
                        <Field
                          name={`roads.${index}.kelirikkoPainorajoitus`}
                          label="tie.kelirikko_painorajoitus"
                          component={TextInput}
                        ></Field>

                        <Row>
                          <Col xs={4}>
                            <Field
                              label="tie.kelirikon_alkaminen"
                              name={`roads.${index}.kelirikkoAlkuKuukausi`}
                              options={monthOptions}
                              component={SelectInput}
                            ></Field>
                          </Col>
                          <Col xs={2}>
                            <Field
                              name={`roads.${index}.kelirikkoAlkuAika`}
                              options={[
                                { value: 'alku', text: 'tie.kelirikko.alku' },
                                { value: 'keski', text: 'tie.kelirikko.keski' },
                                { value: 'loppu', text: 'tie.kelirikko.loppu' }
                              ]}
                              component={SelectInput}
                            ></Field>
                          </Col>

                          <Col xs={4}>
                            <Field
                              label="tie.kelirikon_paattyminen"
                              name={`roads.${index}.kelirikkoLoppuKuukausi`}
                              options={monthOptions}
                              component={SelectInput}
                            ></Field>
                          </Col>

                          <Col xs={2}>
                            <Field
                              name={`roads.${index}.kelirikkoLoppuAika`}
                              options={[
                                { value: 'alku', text: 'tie.kelirikko.alku' },
                                { value: 'keski', text: 'tie.kelirikko.keski' },
                                { value: 'loppu', text: 'tie.kelirikko.loppu' }
                              ]}
                              component={SelectInput}
                            ></Field>
                          </Col>
                        </Row>
                      </>
                    )}
                  </Col>

                  <Col className="border">
                    <div className="mt-3">
                      <Field
                        name={`roads.${index}.ajokielto`}
                        label="tie.ajokielto"
                        component={TextInput}
                      ></Field>
                    </div>

                    <Field
                      name={`roads.${index}.ajokieltoLisakilvet`}
                      label="tie.ajokiellon_lisakilvet"
                      component={TextInput}
                    ></Field>
                  </Col>

                  <Col className="border">
                    <div className="mt-3">
                      <Field
                        name={`roads.${index}.ajoesteTiella`}
                        label="tie.ajoeste_tiella"
                        component={CheckBoxInput}
                      ></Field>
                    </div>
                    {values.roads[index].ajoesteTiella && (
                      <Form.Group as="div">
                        <Row>
                          <Col>
                            <Field
                              name={`roads.${index}.ajoesteTyyppi`}
                              label="tie.ajoesteen_tyyppi"
                              options={[
                                { value: null, text: 'map.select' },
                                { value: 'avattava puomi', text: 'tie.ajoeste1' },
                                { value: 'suljettu yhteys', text: 'tie.ajoeste2' }
                              ]}
                              component={SelectInput}
                            ></Field>
                          </Col>
                        </Row>
                      </Form.Group>
                    )}
                  </Col>

                  <div className="border">
                    <Col className="mt-3">
                      <Field
                        name={`roads.${index}.nopeusRajoitus`}
                        label="tie.nopeusrajoitus"
                        options={[
                          { value: ' ', text: 'map.select' },
                          { value: '20 Km/h', text: 'tie.nopeus20' },
                          { value: '30 Km/h', text: 'tie.nopeus30' },
                          { value: '40 Km/h', text: 'tie.nopeus40' },
                          { value: '50 Km/h', text: 'tie.nopeus50' },
                          { value: '60 Km/h', text: 'tie.nopeus60' }
                        ]}
                        component={SelectInput}
                      ></Field>
                    </Col>
                  </div>

                  <div className="border">
                    <Col className="mt-3">
                      <Field
                        name={`roads.${index}.karttaLinkit`}
                        type="textarea"
                        label="tie.karttalinkit"
                        component={TextInput}
                      ></Field>
                    </Col>
                  </div>

                  <div className="border">
                    <Col className="mt-3">
                      <Field
                        name={`roads.${index}.muutTiedot`}
                        type="textarea"
                        label="tie.muut_merkit_ja_rajoitukset"
                        component={TextInput}
                      ></Field>
                    </Col>
                  </div>
                </Card>
              ))}
              <Row className="mt-3">
                <Col></Col>
                <Col className="col-auto">
                  <Button size="lg" variant="success" onClick={() => push({})}>
                    {t('tie.lisaa_tie')}
                  </Button>
                </Col>
              </Row>
            </>
          )}
        ></FieldArray>

        <Row className="mt-3 mb-5">
          <Col>
            <ButtonGroup>
              <Button onClick={() => setFormPage(2)}>
                <Translation>{t => t('common.previous')}</Translation>
              </Button>
              <Button onClick={() => handlePageSubmit()}>
                <Translation>{t => t('common.next')}</Translation>
              </Button>
            </ButtonGroup>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}

export default Page3b
