import React from 'react'
import { Form, Row, Col } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import InfoSybmol from './utils/InfoSymbol'

const SelectInput = props => {
  const { t } = useTranslation()
  const { label, info, required, options } = props
  const { field, form } = props
  const { errors, touched } = form

  const error = errors[field.name] && touched[field.name] ? errors[field.name] : false
  const isValid = !error && touched[field.name]
  return (
    <Form.Group as="div">
      <Row>
        <Col>
          <Form.Label>
            {t(label)}
            &nbsp;
            {required && <span className="text-muted">({t('common.required')})</span>}
          </Form.Label>
        </Col>
        {info && (
          <Col className="col-auto">
            <InfoSybmol title={t(label)} content={t(info)} />
          </Col>
        )}
      </Row>
      <Form.Control as="select" {...props} {...field} isInvalid={error} isValid={isValid}>
        {options.map(o => {
          return (
            <option key={o.value} value={o.value}>
              {t(o.text)}
            </option>
          )
        })}
      </Form.Control>
      <Form.Control.Feedback type="invalid">{t('common.chooseOne')}</Form.Control.Feedback>
    </Form.Group>
  )
}

export default SelectInput
