import React from 'react'
import { useTranslation } from 'react-i18next'
import { Row, Col, Button } from 'react-bootstrap'

const digiroadLink = 'https://vayla.fi/avoindata/digiroad'

const ExitPage = () => {
  const { t } = useTranslation()

  const redirectUser = () => {
    window.location.href = digiroadLink
  }

  return (
    <Row>
      <Col>
        <Row className="mb-3">
          <Col className="text-center">
            <h2>{t('thankyou.title')}</h2>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col className="text-center">
            <p>{t('thankyou.tarkistaTositeliite')}</p>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col className="text-center">
            <p>{t('thankyou.bodytext')}</p>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col className="text-center">
            <p>{t('thankyou.close_tab')}</p>
          </Col>
        </Row>

        <Row className="text-center">
          <Col>
            <Button type="button" size="lg" variant="primary" onClick={redirectUser}>
              {t('thankyou.redirect')}
            </Button>
          </Col>
        </Row>
      </Col>
    </Row>
  )
}

export default ExitPage
