import React from 'react'
import { Col, Row, Form } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'

const SummaryInfoField = ({ title, content }) => {
  const { t } = useTranslation()
  return (
    <Form.Group>
      <Row>
        <Col>
          <Form.Label className="font-weight-bold">{`${t(title)}:`}</Form.Label>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Label>{content}</Form.Label>
        </Col>
      </Row>
    </Form.Group>
  )
}

export default SummaryInfoField
