import * as Yup from 'yup'

export const schemaObject = {
  roads: Yup.array()
    .of(
      Yup.object().shape({
        tiekunnanTienNimi: Yup.string()
          .min(3)
          .max(50)
          .required(),
        painorajoitus: Yup.boolean().optional(),
        painorajoitus_suurin_massa: Yup.string()
          .max(10)
          .optional(),
        painorajoitus_lisakilvet: Yup.string()
          .max(50)
          .optional(),
        kelirikkoToistuva: Yup.boolean().optional(),
        kelirikkoPainorajoitus: Yup.string()
          .max(100)
          .optional(),
        kelirikkoAlkuKuukausi: Yup.string()
          .max(20)
          .optional(),
        kelirikkoAlkuAika: Yup.string()
          .max(20)
          .optional(),
        kelirikkoLoppuKuukausi: Yup.string()
          .max(20)
          .optional(),
        kelirikkoLoppuAika: Yup.string()
          .max(20)
          .optional(),
        ajokielto: Yup.string()
          .max(100)
          .optional(),
        ajokieltoLisakilvet: Yup.string()
          .max(100)
          .optional(),
        ajoesteTiella: Yup.boolean().optional(),
        ajoeesteTyyppi: Yup.string().optional(),
        nopeusRajotus: Yup.string()
          .max(10)
          .optional(),
        karttaLinkit: Yup.string().max(300),
        muutTiedot: Yup.string().max(300)
      })
    )
    .min(1)
}

export const initialValues = {
  roads: [
    {
      tiekunnanTienNimi: '',
      painorajoitus: false,
      painorajoitus_suurin_massa: '',
      painorajoitus_lisakilvet: '',
      kelirikkoToistuva: false,
      kelirikkoPainorajoitus: '',
      kelirikkoAlkuKuukausi: '',
      kelirikkoAlkuAika: 'alku',
      kelirikkoLoppuKuukausi: '',
      kelirikkoLoppuAika: 'loppu',
      ajokielto: '',
      ajokieltoLisakilvet: '',
      ajoesteTiella: false,
      ajoesteTyyppi: '',
      nopeusRajotus: '',
      karttaLinkit: '',
      muutTiedot: ''
    }
  ]
}

// Page schema for one page at a time validation
export const schema = Yup.object().shape(schemaObject)
export const schemaAsArray = Object.keys(schemaObject)

export default { schema, schemaObject, initialValues, schemaAsArray }
