import * as Yup from 'yup'
import maitenanceAssociation from './maitenanceAssociation'
import digiroadStatus from './digiroadStatus'
import privateRoad from './privateRoad'

export default {
  privateRoadYupSchema: Yup.object().shape({
    ...maitenanceAssociation.schemaObject,
    ...digiroadStatus.schemaObject,
    ...privateRoad.schemaObject
  }),

  privateRoadYupInitialValues: {
    ...maitenanceAssociation.initialValues,
    ...digiroadStatus.initialValues,
    ...privateRoad.initialValues
  }
}
