import React from 'react'
import { Col, Row, Form } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'

const DeliveryMethod = props => {
  const { t } = useTranslation()
  const { email } = props
  const { field, form } = props
  const { errors, touched } = form

  const error = errors[field.name] && touched[field.name] ? errors[field.name] : false
  const isValid = !error && touched[field.name]

  return (
    <div className="mt-6">
      <Row className="mb-4 mt-5">
        <Col>
          <h6>{t('summary.tositeliiteToimitus')}</h6>
        </Col>
      </Row>
      <Row className="mb-4">
        <Col>
          <Form.Control as="select" {...props} {...field} isInvalid={error} isValid={isValid}>
            <option value="">{t('map.select')}</option>
            {email ? (
              <option value="email">{t('summary.tositeliiteOption1')}</option>
            ) : (
              <option value="email" disabled>
                {t('summary.tositeliiteOption1')}
              </option>
            )}

            <option value="download">{t('summary.tositeliiteOption2')}</option>

            {email ? (
              <option value="emailAndDownload">{t('summary.tositeliiteOption3')}</option>
            ) : (
              <option value="emailAndDownload" disabled>
                {t('summary.tositeliiteOption3')}
              </option>
            )}
          </Form.Control>
        </Col>
      </Row>
    </div>
  )
}

export default DeliveryMethod
