import React from 'react'
import { Row, Col, Button, ButtonGroup } from 'react-bootstrap'
import { useTranslation, Translation } from 'react-i18next'
import privateRoad from '../../models/privateRoad'
import { toast } from 'react-toastify'
import { FieldArray, Field } from 'formik'
import TextInput from '../inputs/TextInput'

const Page3a = props => {
  const { t } = useTranslation()
  const { formPage, setFormPage, values } = props

  const handlePageSubmit = async () => {
    try {
      await privateRoad.schema.validate(values)
      setFormPage(5)
    } catch (exeption) {
      toast.warning(t('common.pagevalidationerror'))
    }
  }

  if (formPage !== 3) return null

  return (
    <div>
      <Row className="mt-3">
        <Col>
          <h2>{t('map.already_submitted_no_changes')}</h2>
        </Col>
      </Row>

      <FieldArray
        name="roads"
        render={({ push, remove }) => (
          <>
            {values.roads.map((road, index) => (
              <div key={index}>
                <Row className="border">
                  <Col sm={10} className="mt-3">
                    <Field
                      name={`roads.${index}.tiekunnanTienNimi`}
                      required
                      label="tie.road_name"
                      info="tie.road_name_info"
                      component={TextInput}
                    ></Field>
                  </Col>
                  <Col sm={2} className="d-flex flex-column">
                    <Button className="mt-5" onClick={() => remove(index)} variant="danger">
                      {t('tie.poista_tie')}
                    </Button>
                  </Col>
                </Row>
              </div>
            ))}
            <Row className="mt-3">
              <Col></Col>
              <Col className="col-auto">
                <Button size="lg" variant="success" onClick={() => push({})}>
                  {t('tie.lisaa_tie')}
                </Button>
              </Col>
            </Row>
          </>
        )}
      ></FieldArray>

      <Row className="mt-5 mb-5">
        <Col>
          <ButtonGroup>
            <Button onClick={() => setFormPage(formPage - 1)}>
              <Translation>{t => t('common.previous')}</Translation>
            </Button>
            <Button onClick={() => handlePageSubmit()}>
              <Translation>{t => t('common.next')}</Translation>
            </Button>
          </ButtonGroup>
        </Col>
      </Row>
    </div>
  )
}

export default Page3a
